<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.id"></el-input>
      </el-form-item>
      <el-form-item label="longName">
        <el-input placeholder="longName" v-model="param.longName"></el-input>
      </el-form-item>
      <el-form-item label="homeBase">
        <el-input placeholder="homeBase" v-model="param.homeBase"></el-input>
      </el-form-item>
      <el-form-item label="manager">
        <el-input placeholder="manager" v-model="param.manager"></el-input>
      </el-form-item>
      <el-form-item label="revenue">
        <el-input placeholder="revenue" v-model="param.revenue"></el-input>
      </el-form-item>
      <el-form-item label="ingredientsCarried">
        <el-input placeholder="ingredientsCarried" v-model="param.ingredientsCarried"></el-input>
      </el-form-item>
      <el-form-item label="costCarried">
        <el-input placeholder="costCarried" v-model="param.costCarried"></el-input>
      </el-form-item>
      <el-form-item label="weightCarried">
        <el-input placeholder="weightCarried" v-model="param.weightCarried"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="id"
          prop="id"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="longName"
          prop="longName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="homeBase"
          prop="homeBase"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="manager"
          prop="manager"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="revenue"
          prop="revenue"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="ingredientsCarried"
          prop="ingredientsCarried"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="costCarried"
          prop="costCarried"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="weightCarried"
          prop="weightCarried"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        longName: '',
        homeBase: '',
        manager: '',
        revenue: '',
        ingredientsCarried: '',
        costCarried: '',
        weightCarried: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/displayServiceView/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.el-pagination {
  margin-top: 10px;
}
